@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.navbar-desktop{padding:0px 10%;box-shadow:0 0 5px 0 var(--color-shadow)}@media (max-width: 480px){.navbar-desktop{display:none}}.navbar-mobile{display:none}@media (max-width: 480px){.navbar-mobile{display:block;padding:8px 0px !important;box-shadow:0 0 5px 0 #303337;display:flex;justify-content:space-around;text-align:center}.navbar-mobile .menu .menu__title{color:var(--color-pure-white);font-size:11px;font-weight:300}}

.container-banner{height:100vh;background-image:url(/my-wedding/static/media/prewedding-1.7cb28dd9.jpeg);background-attachment:fixed;background-size:cover;background-position:0 -600px;overflow:hidden}.container-banner .hero-body{display:flex;align-items:flex-end}.container-banner .hero-body .container{margin-bottom:10%}.container-banner .hero-body .container .title{font-weight:700;font-size:40px;color:var(--color-pure-white)}.container-banner .hero-body .container .subtitle{color:var(--color-pure-white)}

.container-about-us{max-width:unset}.container-about-us .title{width:100%;text-align:center}@media (max-width: 480px){.container-about-us .card-person{display:flex;flex-flow:column nowrap;align-items:center}}.container-about-us .card-person .card-person__frame{border:20px solid var(--color-perfect-wood);width:18vw}@media (max-width: 480px){.container-about-us .card-person .card-person__frame{width:200px}}.container-about-us .card-person .card-person__frame .card-person__img{box-shadow:0px 0px 85px 0px rgba(0,0,0,0.14);bottom:22%;left:2vw;top:-4vw;position:relative;display:block}.container-about-us .card-person .card-person__title{text-align:center;color:var(--color-cool-black);font-size:20px;font-weight:700}.container-about-us .card-person .card-person__desc{text-align:center;color:var(--color-cool-black)}

.container-location{max-width:unset}.container-location .title{width:100%;text-align:center}.container-location .card-location{text-align:center}.container-location .card-location .card-location__body .card-location__body--title{color:var(--color-cool-black);font-size:20px;font-weight:700}.container-location .card-location .card-location__body .card-location__body--desc{color:var(--color-cool-black);font-size:12px}

.container-landing{box-shadow:0 3px 6px 0 rgba(0,0,0,0.16)}

:root{--color-primary: #000000;--color-secondary: #dd0000;--color-additional: #ffce00;--color-pure-white: #fff;--color-shadow: #d1d9e6;--color-cool-black: #363636;--color-perfect-wood: rgb(183 160 134)}h1,h2,h3,h4,h5,h6,p{margin:0px !important}*{box-sizing:border-box;margin:0px;padding:0px}*:focus{outline:none}body{height:100vh;padding:0px;font-family:"Poppins", helvetica}body hr{width:20%;margin:2% auto 1% auto;text-align:center;border:0.5px solid #161616 !important;color:#161616 !important}body .d-flex{display:flex}#root{height:100%}

